.menu-item-container .footer-container {
  display: flex;
  gap: 8px;
}
.menu-item-container .collapse-btn {
  display: flex;
  align-items: center;
  box-sizing: border-box;
  width: 16px;
  height: 16px;
  background: transparent;
  border: none;
  box-sizing: border-box;
  margin-top: 4px;
  justify-content: center;
}
.menu-item-container .collapse-btn:focus, .menu-item-container .collapse-btn:active {
  outline: none;
}
.menu-item-container .hint {
  text-align: right;
  color: var(--text-secondary-text-default);
}
.menu-item-container .hint * {
  color: var(--text-secondary-text-default);
}
